* {
  box-sizing: border-box;
  margin: 0;
}

#root {
  position: relative;
  font-family: 'Allison', cursive;
  font-size: 50px;
  color: white;
  background-color: #3182ce;
}

.app {
  min-height: 100vh;
  max-width: 800px;
  padding: 0 100px;
  margin: 0 auto;
}

.info {
  display: flex;
  justify-content: center;
  align-items: center;

  margin-bottom: 40px;
  padding-top: 40px;
}

.title {
  font-size: 40px;
  font-weight: 400;
  margin-right: 50px;
}

.wins-losses {
  display: flex;
  align-items: center;
  gap: 60px;
}

.losses > .number {
  color: red;
}
.wins > .number {
  color: lawngreen;
}

.number {
  font-size: 80px;
  margin-right: 5px;
}

.text {
  font-size: 30px;
}

.choices__title,
.choices-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
}
.choices__title {
  font-size: 40px;
  color: rgba(255, 255, 255, 0.281);
}

.btn-game,
.computer__btn {
  display: block;

  width: 120px;
  height: 120px;
  padding: 25px;
  margin-bottom: 10px;

  border-radius: 50%;

  cursor: pointer;
}

.btn-game:hover {
  transform: scale(1.1);
}
.btn-game:active {
  transform: scale(1);
}

.rock {
  background-color: #ed8936;
  border: 1px solid #ed8936;
}
.paper {
  background-color: #48bb78;
  border: 1px solid #48bb78;
}
.scissors {
  background-color: #9f7aea;
  border: 1px solid #9f7aea;
}

.computer__btn {
  background-color: black;
  border: 1px solid black;

  font-size: 60px;
  color: rgba(252, 250, 250, 0.281);
}

.vs,
.computer,
.you,
.you__title,
.computer__title {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 140px;
}

.vs {
  transform: rotate(-45deg);

  font-size: 150px;
  color: rgba(255, 255, 255, 0.281);
}

.game-state {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: 100vh;

  background-color: #4299e1a1;

  cursor: pointer;
}

.game-state__wrap {
  position: relative;
}

.game-state-content {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  gap: 10px;

  width: 450px;
  height: 200px;
  border-radius: 20px;
}

.game-state-content > p {
  width: 120px;

  font-size: 80px;
  line-height: 0.9;
  color: white;

  text-align: center;
}

.game-state-content__icon {
  width: 100px;
}

.win {
  background-color: green;
}
.lose {
  background-color: red;
}
.draw {
  background-color: black;
}

.restartGame {
  position: absolute;
  bottom: -30px;
  left: 16%;

  width: 300px;
  height: 57px;

  background-color: #ebf8ff;

  border-radius: 10px;
  outline: none;
  border: none;

  font-size: 24px;
  color: #1a365d;

  cursor: pointer;
}

.restartGame:active {
  transform: scale(0.95);
}
